<template>
  <div id="collection-create-dialog-component">
    <el-dialog
      title="Create Collection"
      v-model="visible"
      width="40%"
      top="45px"
    >
      <div class="inputs-container">
        <el-row :gutter="30">
          <el-col :span="24">
            <el-select v-model="form.status">
              <el-option
                v-for="item in statusOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="24">
            <p class="label">Collection Name</p>
            <el-input v-model="form.name" clearable> </el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Owner</p>
            <el-input v-model="form.owner" clearable> </el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Description (optional)</p>
            <el-input v-model="form.description" clearable> </el-input>
          </el-col>
          <el-col :span="24">
            <el-row :gutter="40">
              <el-col class="nmb" :span="12">
                <p class="label">Year (optional)</p>
                <el-select v-model="form.year">
                  <el-option
                    v-for="item in yearOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-col>
              <el-col class="nmb" :span="12">
                <p class="label">Season (optional)</p>
                <el-select v-model="form.season">
                  <el-option
                    v-for="item in seasonOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-col>
            </el-row>
          </el-col>
          <el-col :span="24">
            <p class="label">Brand (optional)</p>
            <el-input v-model="form.brand" clearable> </el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Sport (optional)</p>
            <el-input v-model="form.sport" clearable> </el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Theme (optional)</p>
            <el-input v-model="form.theme" clearable> </el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Hashtag (optional)</p>
            <el-input v-model="form.hashtag" clearable> </el-input>
          </el-col>
        </el-row>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button type="info" @click="dialogCreateVisible = false"
            >Cancel</el-button
          >
          <el-button type="success" @click="onCreateConfirm">
            Done
          </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from "vue";
import { useStore } from "vuex";

import { CollectionCreateInputs } from "@/typings/collection";
import { OptionItem } from "@/typings/asideFilter";
import { PostCreateCollection } from "@/typings/store";

const statusOptions: OptionItem[] = [
  { label: "Public", value: "public", checked: false },
  { label: "Private", value: "private", checked: false }
];

const yearOptions: OptionItem[] = [
  { label: "2020", value: 2020, checked: false },
  { label: "2021", value: 2021, checked: false },
  { label: "2022", value: 2022, checked: false },
  { label: "2023", value: 2022, checked: false }
];

const seasonOptions: OptionItem[] = [
  { label: "Spring", value: "Spring", checked: false },
  { label: "Summer", value: "Summer", checked: false },
  { label: "Fall", value: "Fall", checked: false },
  { label: "Winter", value: "Winter", checked: false }
];

const Component = defineComponent({
  name: "CreateDialog",
  setup(_, { emit }) {
    const store = useStore();

    const visible = ref(false);
    const form = reactive<CollectionCreateInputs>({
      status: "private",
      name: "",
      owner: "",
      description: "",
      year: "",
      season: "",
      brand: "",
      sport: "",
      theme: "",
      hashtag: ""
    });

    const onCreateConfirm = async () => {
      const params = {
        name: form.name,
        description: form.description,
        year: form.year,
        season: form.season,
        theme: form.theme,
        hash_tag: form.hashtag,
        status: form.status,
        owner: form.owner,
        // TODO remove creator when api fixed.
        creator: 4,
        sport_type: []
      };
      await store.dispatch(PostCreateCollection, params);
      visible.value = false;
      emit("load");
    };

    const open = () => {
      visible.value = true;
    };

    return {
      form,
      visible,
      statusOptions,
      yearOptions,
      seasonOptions,

      open,
      onCreateConfirm
    };
  }
});

export default Component;
</script>

<style scoped lang="stylus">
#collection-create-dialog-component
  .inputs-container
    padding 0 10%
    .label
      text-align start
      font-size $font-size-large
      font-weight 500
      padding 0 8px
      margin-bottom 15px

    .nmb
      margin-bottom 0 !important

    :deep() .el-col
      margin-bottom 30px

    :deep() .el-select
      width 100%
</style>
