
import { defineComponent, toRefs, computed, PropType } from "vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";
import * as _ from "ramda";

import { ElNotification } from "element-plus";
import defaultImage from "@/assets/images/default-image.png";
import { CollectionCardResponse } from "@/typings/collection";
import { PostCollectionDuplicate } from "@/typings/store";
import { formatImageUrl } from "@/utils/image";

const Component = defineComponent({
  name: "CollectionCard",
  props: {
    data: {
      type: Object as PropType<CollectionCardResponse>,
      default: () => ({})
    },
    hasButtons: {
      type: Boolean,
      default: false
    }
  },
  setup(props, { emit }) {
    const router = useRouter();
    const store = useStore();
    const { data, hasButtons } = toRefs(props);

    // 需要剛好顯示四張圖片，所以要讓陣列長度剛好等於 4,
    // 後面補 null 的部分會被替換成預設圖片
    const images = computed(() => {
      const _images = _.splitAt(4, data.value.fabric_images)[0];
      if (_images.length === 4) return _images;
      return _.splitAt(4, [..._images, null, null, null, null])[0];
    });

    const onImageClick = async () => {
      await router.push({
        name: "CollectionDetail",
        params: { id: data.value.id }
      });
    };

    const onDuplicateClick = async () => {
      if (!hasButtons) return;
      try {
        const params = {
          // 注意第一項名稱沒統一
          fabrics: data.value.fabric,
          name: data.value.name,
          description: data.value.description,
          year: data.value.year,
          season: data.value.season,
          theme: data.value.theme,
          hash_tag: data.value.hash_tag,
          status: data.value.status,
          creator: data.value.creator,
          owner: data.value.owner,
          company: data.value.company,
          joined_member: data.value.joined_member,
          sport_type: data.value.sport_type
        };
        await store.dispatch(PostCollectionDuplicate, params);
        ElNotification({
          type: "success",
          title: "Success",
          message: `Duplicate ${data.value.name} successful.`
        });
        emit("load");
      } catch (e) {
        ElNotification({
          type: "error",
          title: "Error",
          message: `Duplicate ${data.value.name} Failed.`
        });
      }
    };

    return {
      images,
      defaultImage,

      onImageClick,
      onDuplicateClick,
      formatImageUrl
    };
  }
});

export default Component;
